import Vue from 'vue'
import Router from 'vue-router'

import Hello from '@/components/Hello'
import Grid from '@/components/Grid'
import Login from '@/components/Login'
Vue.use(Router)

export default new Router({
  routes: [

    {
      path: '/',
      name: 'Grid',
      component: Grid
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/hello',
      name: 'Hello',
      component: Hello
    }
  ]
})



